"use strict";
toggle_button.onclick = function () {
    if (navigation_menu.className === "col-lg-8 col-md-12 col-xs-12 navigation-menu") {
        navigation_menu.className += " toggle"
        toggle_icon.className = "fa fa-chevron-up"
    } else {
        navigation_menu.className = "col-lg-8 col-md-12 col-xs-12 navigation-menu";
        toggle_icon.className = "fa fa-bars"
    }
}